                                    desc_es: '<p>La aplicacin de meta-anlisis (MA) permite el anlisis combinado de un conjunto de estudios sobre un tema de inters, cuyos resultados son obtenidos de forma independiente. La finalidad de evaluar estos resultados conjuntamente es realizar una sntesis y obtener estimaciones globales de los efectos de inters con mtricas apropiadas. La tcnica puede ser aplicada para sintetizar estudios de QTL y posicionar con mayor evidencia estadstica loci asociados a caracteres de importancia agronmica. Luego de una revisin sistemtica de estudios de mapeo de QTL, tanto en poblaciones biparentales como el mapeo asociativo en poblaciones diversas, el MA se aplica para consolidar hallazgos y obtener estimaciones globales. El objetivo del presente curso es ofrecer una visin de los principios que rigen la sntesis de la evidencia cientfica en estudios genticos mediante aplicaciones de rutinas de software que permiten realizar MA a partir de una recopilacin de estudios de QTL en poblaciones vegetales. Tambin se extender el uso de la tcnica de MA hacia la estimacin de otros efectos de inters en estudios de asociacin gentica.</p>',
