                        autor: 'Stefan De Haan. Centro Internacional de la Papa (CIP). Per.',
                        autorDesc: '<p>Stef de Haan trabaja como investigador principal en el Centro Internacional de la Papa (CIP) en Per. Tiene un PhD en Biosistemtica y MSc en Agroecologa. Tiene un inters particular en el manejo contemporneo y el uso in-situ de recursos genticos de cultivos con un enfoque geogrfico en los Andes y el sudeste asitico. Su principal enfoque de investigacin ha sido comprender la conservacin y el uso de los recursos genticos bajo la gestin de los agricultores, incluso la evolucin continua, sistemas de semillas, patrones geoespaciales, la nutricin humana y acuerdos de distribucin de beneficios. Actualmente coordina la Iniciativa Andina del CIP con un enfoque en la agrobiodiversidad, los sistemas alimentarios y el cambio climtico.</p>'
