                        titulo_es: 'Conferencia Danko Brncic, SOCHIGEN: Escudriando el pasado de las poblaciones originarias y mestizas del Cono Sur de Sudamrica mediante marcadores genticos de herencia uniparental. Mauricio Moraga Vergara.',
