                        tipo: 'conferencia',
                        horario: '12:00  13:00',
                        lugar: 'Bustelo. Sala 1',
                        autor: 'Mauricio Moraga Vergara. Universidad de Chile. Chile.',
                        autorDesc: '<p>Doctor en Bioqumica, Universidad de Chile; Bioqumico, Universidad de Chile. Profesor Asociado, Programa de Gentica Humana, Instituto de Ciencias Biomdicas, Facultad de Medicina, y Departamento de Antropologa, Facultad de Ciencias Sociales, Universidad de Chile.  Past president Sociedad de Gentica de Chile, 2012-2013. Past president Asociacin Latinoamericana de Antropologa Biolgica, 2014-2016. Su lnea de investigacin principal ha estado centrada en el estudio de las poblaciones originarias y mestizas de Chile y Sudamrica, mediante el uso de marcadores de herencia uniparental (ADN mitocondrial y cromosoma Y) as como autosmicos.  Ha trabajado tanto con muestras actuales como con ADN antiguo ayudando a entender las dinmicas del poblamiento de la Patagonia occidental. </p>'
